import FingerprintJS from '@fingerprintjs/fingerprintjs'

// Initialize an agent at application startup.
const fpPromise = FingerprintJS.load()

function logMetrics(guestName) {

    ; (async () => {
        const fp = await fpPromise
        const result = await fp.get()
        submitMetrics(guestName, result.visitorId);
    })()
}

function submitMetrics(guestName, fingerprint) {

    var requestHeaders = new Headers();
    requestHeaders.append("content-type", "application/x-www-form-urlencoded;");

    var urlEncodedBody = new URLSearchParams();
    urlEncodedBody.append("entry.35346480", fingerprint);
    urlEncodedBody.append("entry.1489285040", guestName);
    var requestOptions = {
        method: 'POST',
        mode: 'no-cors',
        headers: requestHeaders,
        body: urlEncodedBody,
    };

    var url = "https://docs.google.com/forms/u/0/d/e/1FAIpQLSd7jcxia-uXO6lAc-mlq9jjzgF4MFaiz2Ey5z-AP-C1h7YM4w/formResponse";

    fetch(url, requestOptions)
        .then(response => {
            if (response.status === 0 || response.status === 200) {
                console.log("Metrics Logged");
            } else {
                console.warn("Metrics not logged");
            }
        });
}

export default logMetrics;