import React, { useState } from "react"

import Form from "react-bootstrap/Form"
import Button from "react-bootstrap/Button"
import styles from "./RSVP.module.scss"
import { Alert, Spinner } from "react-bootstrap";

import submitRSVP from "services/rsvp-service.js"

const RSVPForm = ({ currentGuestName, numberOfGuests }) => {
    const [attending, setAttending] = useState(null);
    const [numberOfGuestsAttending, setNumberOfGuestsAttending] = useState(numberOfGuests);
    const [guestList, setGuestList] = useState({});
    const [rsvpRequestStatus, setRSVPRequestStatus] = useState('not_submitted');

    const onRSVPSuccess = (response) => {
        setRSVPRequestStatus('submitted');
    }

    const onRSVPFailure = (error) => {
        setRSVPRequestStatus('error');
    }

    const onFormSubmit = (form) => {

        form.preventDefault();
        setRSVPRequestStatus('submitting');

        const formTarget = new FormData(form.target);
        let formData = Object.fromEntries(formTarget.entries())

        // Convert guestListObject csv seperated format.
        const numberOfGuests = formData["numberOfGuests"];
        let guestListCSV = '-';
        if (numberOfGuests > 1) {
            guestListCSV = [...Array(numberOfGuests - 1).keys()].map((id) => guestList[id]).join(" , ");
        }

        formData['name'] = currentGuestName;
        formData['guestList'] = guestListCSV;

        sendRSVPRequest(formData, onRSVPSuccess, onRSVPFailure);
    }

    let promptForElephants = false;

    if(currentGuestName === 'Josh Garvin' || currentGuestName === 'Sravanthi Dornadula') {
        promptForElephants = true;
    }

    return (
        <>
            <div className={styles.formbox}>
                <Form 
                    onSubmit={(e) => onFormSubmit(e)}
                    onChange={() => setRSVPRequestStatus('not_submitted')}>

                    <Form.Group className="mb-3" controlId="formName">
                        <Form.Label>Name</Form.Label>
                        <Form.Control disabled placeholder={currentGuestName} name="name" />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formAttending">
                        <Form.Label>Attending?</Form.Label>
                        <Form.Select onChange={(e) => setAttending(e.target.value)} name="attending" required>
                            <option></option>
                            <option value="yes">Yes</option>
                            <option value="no">No</option>
                        </Form.Select>
                    </Form.Group>

                    {attending === "yes" &&
                        <Form.Group className="mb-3" controlId="formEmail">
                            <Form.Label>Email address</Form.Label>
                            <Form.Control type="email" placeholder="Enter email" name="email" required />
                        </Form.Group>
                    }

                    {attending === "yes" &&
                        <Form.Group className="mb-3" controlId="formPhoneNumber">
                            <Form.Label>Phone number</Form.Label>
                            <Form.Control type="text" name="phoneNumber" required />
                        </Form.Group>
                    }

                    {attending === "yes" &&
                        <Form.Group className="mb-3" controlId="formAddress">
                            <Form.Label>Mailing address</Form.Label>
                            <Form.Control type="text" name="mailingAddress" required />
                        </Form.Group>
                    }

                    {attending === "yes" &&
                        <Form.Group className="mb-3" controlId="formNumberOfGuests">
                            <Form.Label>How many total guests in your party will be attending?</Form.Label>
                            <Form.Select onChange={(e) => setNumberOfGuestsAttending(e.target.value)} defaultValue={numberOfGuestsAttending} name="numberOfGuests" required>
                                {[...Array(10).keys()].map((id) => (
                                    <option
                                        value={id + 1}
                                        key={`guest-${id}`}>
                                        {id + 1}
                                    </option>
                                ))}
                            </Form.Select>
                        </Form.Group>
                    }

                    {attending === "yes" && numberOfGuestsAttending > 1 &&
                        <Form.Group className="mb-3">
                            <Form.Label key="label">
                                Please list the names of the guests attending
                            </Form.Label>
                            <Form.Group className="mb-3">
                                <Form.Control disabled placeholder={currentGuestName} name="guestName" />
                            </Form.Group>
                            {guestNames(numberOfGuestsAttending, guestList, setGuestList)}
                        </Form.Group>
                    }

                    {attending === "yes" &&
                        <Form.Group className="mb-3" controlId="formDietaryRestrictions">
                            <Form.Label>Any dietary restrictions?</Form.Label>
                            <Form.Control type="text" name="dietaryRestrictions" />
                        </Form.Group>

                    }

                    {attending === "yes" &&
                        <Form.Group className="mb-3" controlId="formSongRequests">
                            <Form.Label>Any song requests to be played at the reception or sangeet?</Form.Label>
                            <Form.Control type="text" name="songRequests" />
                        </Form.Group>

                    }

                    {attending === "yes" &&
                        <Form.Group className="mb-3" controlId="additionalNotes">
                            <Form.Label>Anything else we should know?</Form.Label>
                            <Form.Control type="text" name="additionalNotes" />
                        </Form.Group>

                    }

                    {attending === "yes" && promptForElephants &&
                        <Form.Group className="mb-3" controlId="additionalNotes">
                            <Form.Label>How many elephants would you like ?</Form.Label>
                            <Form.Select defaultValue={numberOfGuestsAttending} name="numberOfElephants" required>
                                <option value="1">One</option>
                                <option value="2">Two</option>
                                <option value="3">Three</option>
                                <option value="5">Five</option>
                                <option value="7">Dont get greedy Josh</option>
                            </Form.Select>
                        </Form.Group>

                    }   

                    <Button variant="light" type="submit">
                        Submit RSPV
                    </Button>
                    {rsvpRequestStatus === 'submitting' &&
                        <Spinner
                            animation="border"
                            size="sm"
                            className={styles.spinner}
                        />
                    }

                    <br />
                    <br />
                    {
                        rsvpRequestStatus === 'submitted' &&
                        <Alert variant="info">
                            Thank you for response. You can update your responses any time.
                        </Alert>
                    }

                    {
                        rsvpRequestStatus === 'error' &&
                        <Alert variant="danger">
                            Oh, no! We were unable to record your request. Maybe try submitting again.
                            If you are still having trouble, please&nbsp;
                            <a href="mailto:hello@colinandtanay.com">
                                email us
                            </a>.
                        </Alert>
                    }
                </Form>
            </div>
        </>
    )
};



const sendRSVPRequest = (formData, onRSVPSuccess, onRSVPFailure) => {
    let rsvpData = {
        "name": formData["name"],
        "email": formData["email"] || '-',
        "phoneNumber": formData["phoneNumber"] || '-',
        "mailingAddress": formData["mailingAddress"] || '-',
        "numberOfGuests": formData["numberOfGuests"] || '-',
        "guestList": formData["guestList"] || '-',
        "dietaryRestrictions": formData["dietaryRestrictions"] || '-',
        "attending": formData["attending"] || '-',
        "songRequests": formData["songRequests"] || '-',
        "additionalNotes": formData["additionalNotes"] || '-'
    };
    submitRSVP(rsvpData, onRSVPSuccess, onRSVPFailure);
}

const guestNames = (numberOfGuestsAttending, currentGuestList, setGuestList) => {
    return (
        [...Array(numberOfGuestsAttending - 1).keys()].map((id) => (
            <Form.Control
                className="mb-3"
                key={`guest-${id}`}
                type="text"
                required
                onChange={(e) => {
                    currentGuestList[`${id}`] = e.target.value;
                    setGuestList(currentGuestList)
                }} />
        ))
    )
}

export default RSVPForm
