function submitRSVP(rsvp, success, fail) {

    var requestHeaders = new Headers();
    requestHeaders.append("content-type", "application/x-www-form-urlencoded;");

    var urlEncodedBody = new URLSearchParams();
    urlEncodedBody.append("entry.1486391250", rsvp["name"]);
    urlEncodedBody.append("entry.1588353019", rsvp["email"]);
    urlEncodedBody.append("entry.930913346",  rsvp["phoneNumber"]);
    urlEncodedBody.append("entry.2084909800", rsvp["mailingAddress"]);
    urlEncodedBody.append("entry.1551118630", rsvp["numberOfGuests"]);
    urlEncodedBody.append("entry.1331608678", rsvp["guestList"]);
    urlEncodedBody.append("entry.1593087314", rsvp["dietaryRestrictions"]);
    urlEncodedBody.append("entry.1663113440", rsvp["attending"]);
    urlEncodedBody.append("entry.2045688263", rsvp["songRequests"]);
    urlEncodedBody.append("entry.558263701", rsvp["additionalNotes"]);

    var requestOptions = {
        method: 'POST',
        mode: 'no-cors',
        headers: requestHeaders,
        body: urlEncodedBody,
    };

    var url = "https://docs.google.com/forms/u/0/d/e/1FAIpQLScJxB0adUTl_ISRB5SoHB0Z03zpBFXnuAbxws4KRdIJ4tN3-A/formResponse";

    fetch(url, requestOptions)
        .then(response => {
            if (response.status === 0 || response.status === 200) {
                return true;
            } else {
                throw Error(response)
            }
        })
        .then(response => success(response))
        .catch(error => fail(error));
}

export default submitRSVP;