import React from "react"

import { TextBlock } from "components/Text Block"
import RSVPForm from "./RSVPForm.js"

const extractFirstNamesAsString = ({ names }) => {
    return names.map((name) => name.split(' ')[0]).join(", ");
}

const AttendingView = (names) => {

    const message = `Guest List: ${extractFirstNamesAsString(names)}`;
    return (
        <>
            <TextBlock
                text={"Thank you for RSVP'ing. We are excited to have you at our wedding!"}
                textStyle={"p"}>
            </TextBlock>
            <TextBlock
                text={message}
                textStyle={"p"}>``
            </TextBlock>
        </>
    )
};

const NotAttendingView = () => {
    return (
        <>
            <TextBlock
                text={"We are sorry you are unable to make it to our wedding. You will be missed! "}
                textStyle={"p"}>
            </TextBlock>
        </>
    )
};

const RSVP = ({ loginDetails }) => {

    let view;

    if (loginDetails.invitation.RSVP_STATUS === 'ATTENDING') {
        view = <AttendingView names={loginDetails.invitation.NAMES} />;
    }
    else if (loginDetails.invitation.RSVP_STATUS === 'NOT_ATTENDING') {
        view = <NotAttendingView />;
    }
    else {
        view = <>
            <TextBlock
                text="RSVP by September 1st, 2022" textStyle="p">
            </TextBlock>
            <br />
            <RSVPForm currentGuestName={loginDetails.currentGuestName}
                numberOfGuests={loginDetails.invitation.NUMBER_OF_GUESTS}
                names={loginDetails.invitation.NAMES} />
        </>;
    }

    return (
        <>
            {view}
        </>
    )
};

export default RSVP
